<template>
  <div class="history">
  
    <top-tab></top-tab>

    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-grid :border="false" :column-num="1" >
          <h2 class="living">正在直播</h2>
          <van-grid-item v-for="item in list" :key="'living' + item" :title="item">
            <div class="van-hairline--top"></div>
            <div class="live-item">
              <div class="live-img">
                <img src="https://img.yzcdn.cn/vant/apple-1.jpg" alt="封面图">

                <div class="live-title">
                  <div class="user-info">
                    <span class="user-name">斗鱼鲍鲍</span>
                    <span class="user-title">宇宙第一烬</span>
                    <span class="live-type">英雄联盟·158万</span>
                  </div>
                </div>
              </div>
            </div>
          </van-grid-item>
          <h2 class="offline">离线</h2>
          <van-grid-item v-for="item in list" :key="'offline' + item" :title="item">
            <div class="van-hairline--top"></div>
            <div class="live-item item-offline">
              <div class="live-img">
                <img src="https://img.yzcdn.cn/vant/apple-1.jpg" alt="封面图">

                <div class="live-title">
                  <div class="user-info">
                    <span class="user-name">斗鱼鲍鲍</span>
                  </div>
                </div>
              </div>
            </div>
          </van-grid-item>
        </van-grid>
      </van-list>
    </van-pull-refresh>

  </div>
  
</template>

<script>
import { mapState, mapMutations } from "vuex";
import {log} from '../utils/log.js'
export default {
  name: "youlike",
  components: {
    // SideBar,
  },
  data() {
    return {
      active: 0,
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  created() {
    let path = this.$route.path
    log('path history ', path)
    this.setState({'path': path})
    this.setState({'isShowSetPage': false})
  },
  computed: {
    ...mapState({
      home: state => state.home
    })
  },
  watch: {
    // cityname: function() {
    //   log('watch cityname this.home.city ', `(${this.cityname})`, `(${this.home.city})`)
    //   let area = this.getcitycode(this.cityname)
    //   this.area = area
    //   this.setState({'area': this.area})
    // },
  },

  activated() {

  },
  
  mounted() {

  },
  methods: {
    ...mapMutations(["setState"]),
    // getcityname(citycode) {
    //   for (const i in this.citylist) {
    //     if (citycode == this.citylist[i].code) {
    //       this.cityname = this.citylist[i].name;
    //     }
    //   }
    // },
    showSetPage() {
      // this.isShowSetPage = !this.isShowSetPage
    },
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 2; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 4) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
  },
  
};
</script>

<style lang="scss" scoped>

  .top-tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    background-color: aquamarine;
  }

  .living,.offline {
    font-size: 18px;
    color: #000000;
    padding: 8px;
  }

  .live-item {
    width: 100%;
    .live-img {
      display: flex;
      // background-color: burlywood;
      padding: 16px 10px;
      img {
        height: 80px;
        width: 120px;
        margin-right: 14px;
      }
      .live-title {
        .user-info {
          display: flex;
          height: 100%;
          flex-direction: column;
          justify-content: space-between;
          color: #8b8b8b;
          .user-name {
            font-size: 18px;
            font-weight: 700;
            color: #000000;
            margin-bottom: 4px;
          }
          .user-title {
            font-size: 16px;
          }
          .live-type {
            font-size: 14px;
          }
        }
      }
    }
  }

  .item-offline {
    .live-img {

      img {
        width: 50px;
        height: 50px;
      }
    }
    .live-title {
      .user-info {
        justify-content: unset !important;
        flex-direction: unset !important;
        align-items: center !important;
        .user-name {
          margin-bottom: 0 !important;
        }
      }
    }
  }


  .history {
    background-color: #eeeeee;
    /deep/ .van-nav-bar::after {
      border: unset;
    }
    /deep/ .van-grid {
      margin-top: 46px;
    }
    /deep/ .van-grid-item__content--center {
      align-items: flex-start;
    }
    /deep/ .van-grid-item__content {
      padding: unset;
    }
    /deep/ .van-tabs__wrap {
      height: 40px;
    }
    /deep/ .van-tab {
      line-height: 40px;
    }
    /deep/ .van-sticky {
      margin-top: 40px;
      /deep/ .van-tabs__wrap::after {
        border: unset;
      }
    }
    /deep/ .van-icon {
      color: #8b8b8b;
    }
  }
  
</style>
